@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tpage .jumbotron .container h1{
    font-family: 'Shadows Into Light', cursive;
    font-size: 100px;
    font-weight: bolder;
}

.tpage .container-tpage{
    width: 65%;
}

.tpage .container-tpage .d-cards{
    justify-content: center;
}

.tpage .container-tpage .d-cards .row{
    width: auto;
    justify-content: center;
}

.tpage .container-tpage .d-cards .tPage-btns-row .card{
    width: 75%;
    flex-direction: row;
    border: none;
}

.tpage .container-tpage .d-cards .row .card .row{
    flex-direction: roגw;
    margin-left: auto;
    margin-right: 0;
}

.tpage .container-tpage .d-cards .row .card .row .btn{
