}



.r-TodoCard{
    justify-content: center;
}

.r-TodoCard .card{
    width: 75%;
    height: 50px;
    align-items: center;
    flex-direction: row;
    text-align: left;

}

.r-TodoCard .form-check-input:checked + .form-check-label{
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
}

.r-TodoCard .card .btn{
    display: none;
    margin-left: auto;
    border-radius: 50%;
    color: red;
    background-color: rgba(220, 220, 220, 0.363);
    border-color: transparent;
}

.r-TodoCard:hover .btn{
    display: inline-block;
}
